<template>
  <div>
    <!--<van-list-->
      <!--v-model="isLoading"-->
      <!--:finished="isFinish"-->
      <!--finished-text="没有了更多了，欢迎继续计划噢！"-->
      <!--@load="onLoad">-->

      <!--class是material-design会有clickWater属性-->
      <div style="position: relative"
           :class="item.status === 1 ? 'plan-single-issue' : 'plan-single-issue-solve'"
           v-for="(item, index) in plans"
           @touchstart="selectItem(item)"
           :key="index + '_' + item.id">
        <div>
          <div class="close-icon-two" @click="changePlan(item)" :style="item.status === 1 ? 'color: green' : 'grey'"><el-icon class="el-icon-edit-outline"></el-icon></div>
          <div class="close-icon" @click="deletePlan(item)" :style="item.status === 1 ? 'color: green' : 'grey'"><el-icon class="el-icon-error"></el-icon></div>
        </div>
        <div class="oct-flex-col-v-center" v-if="item.type === 1" style="margin-left: 15px">
          <div>
            <div class="oct-flex-col">
              <div class="plan-issue-date">{{$moment(item.startTime).format('HH:mm')}} - {{$moment(item.endTime).format('HH:mm')}}</div>
              <div>
                <el-tag  effect="dark" style="margin: 5px 5px 10px 0" type="success" size="mini">{{item.subject}}</el-tag>
                <el-tag  effect="dark" style="margin: 5px 5px 10px 0" :type="item.progress === 0 ? 'danger' : 'primary'" size="mini">{{'自我评价: ' + (item.progress === 0 ? '未完成' : item.progress + '分')}}</el-tag>
                <el-tag  effect="dark" style="margin: 5px 5px 10px 10px" type="info" size="mini">当日计划</el-tag>
              </div>
            </div>
            <div class="plan-issue-date-small">{{$moment(item.startTime).format('YYYY年MM月DD日')}}</div>
            <div class="plan-issue-date-content">{{item.content}}</div>
          </div>
        </div>
        <div v-else class="oct-flex-col-v-center" style="margin-left: 15px">
          <div>
            <div>
              <div class="plan-issue-date-medium">{{$moment(item.startTime).format('YYYY年MM月DD日')}} - {{$moment(item.endTime).format('YYYY年MM月DD日')}}</div>
              <div>
                <el-slider
                  style="width: 100%;margin-left: 10px;"
                  v-model="item.progress"
                  :max="100"
                  :min="0"
                  :step="20"
                  @change="handleSavePrgress"
                  :show-tooltip="false"
                  show-stops>
                </el-slider>
              </div>
              <el-tag  effect="dark" style="margin: 5px 5px 10px 0" type="success" size="mini">{{item.subject}}</el-tag>
              <el-tag  effect="dark" style="margin: 5px 5px 10px 0" type="primary" size="mini">{{'进度:' + item.progress + '%'}}</el-tag>
              <el-tag  effect="dark" style="margin: 5px 5px 10px 0" type="info" size="mini">阶段计划</el-tag>
            </div>
            <div class="plan-issue-date-content">{{item.content}}</div>
          </div>
        </div>
        <div @click="solvePlan(item)" :class="item.status === 1 ? 'plan-solve' : 'plan-solved'"></div>
        <div @click="solvePlan(item)" class="plan-solve-content">{{item.status === 1 ? '打卡' : '重做'}}</div>
      </div>
      <van-dialog
        v-model="showDialog"
        title="任务完成度"
        confirmButtonText="打卡"
        cancelButtonText="取消"
        :closeOnClickOverlay="true"
        :closeOnPopstate="true"
        @confirm="confirmSolve"
        show-cancel-button
      >
        <div style="padding: 10px;">
          <el-slider
            style="width: 90%;"
            v-model="currPlan.progress"
            :max="100"
            :min="0"
            :step="20"
            @change="handleSavePrgress"
            :show-tooltip="false"
            show-stops>
          </el-slider>
        </div>
      </van-dialog>
      <div class="oct-flex-row-all-center"><div style="color: rgba(165, 145, 108, 0.8)">没有更多了</div></div>
    <!--</van-list>-->
  </div>
</template>

<script>
import { changePlanStatus, deletePlan, saveProgress } from '@/api/study'
import { Dialog } from 'vant'

export default {
  name: 'OctAllPlan',
  props: {
    plans: {
      type: Array,
      default: () => []
    }
    // total: {
    //   type: Number,
    //   default: () => 0
    // },
    // loading: {
    //   type: Boolean,
    //   default: () => false
    // },
    // finished: {
    //   type: Boolean,
    //   default: () => false
    // }
  },
  data () {
    return {
      progress: 0,
      showDialog: false,
      initClickWater: false,
      currPlan: {}
      // isFinish: false,
      // isLoading: false
    }
  },
  // watch: {
  //   loading (newVal) {
  //     this.isLoading = newVal
  //   },
  //   finished (newVal) {
  //     this.isFinish = newVal
  //   }
  // },
  mounted () {
    this.plans.forEach((item, index, arr) => {
      this.$set(this.plans[index], 'progress', item.progress)
    })
  },
  methods: {
    // onLoad () {
    //   this.$emit('nextPage', this.isLoading, this.isFinish)
    // },
    confirmSolve () {
      const data = {
        planId: this.currPlan.planId
      }
      if (this.currPlan.status === 1) {
        data.status = 2
      } else {
        data.status = 1
      }
      this.changePlanStatus(data)
    },
    selectItem (item) {
      this.currPlan = item
    },
    async handleSavePrgress (progress) {
      const data = {
        progress: progress,
        planId: this.currPlan.planId
      }
      await saveProgress(data).catch(() => {
        this.$notify.error('调整计划进度失败！')
      })
    },
    getTimeStr (item, format) {
      item = new Date(item)
      return this.$moment(item).format(format)
    },
    solvePlan (item) {
      if (item.type === 1 && item.status === 1) {
        this.currPlan = item
        this.showDialog = true
      } else {
        // 1是去打卡 2 是取消打卡
        const data = {
          planId: item.planId
        }
        if (item.status === 1) {
          data.status = 2
        } else {
          data.status = 1
        }
        this.changePlanStatus(data)
      }
    },
    async changePlanStatus (data) {
      await changePlanStatus(data)
        .then(() => {
          this.$emit('reload')
        })
    },
    deletePlan (item) {
      Dialog.confirm({
        title: '删除计划',
        message: '确认删除计划?'
      }).then(() => {
        this.handleDeletePlan(item)
      }).catch(() => {
      })
    },
    changePlan (item) {
      if (item.type === 1) {
        this.$router.push(this.$router.push({
          name: 'OctAddPlan',
          params: {
            mode: 'change',
            plan: item
          }
        }))
      } else {
        this.$router.push(this.$router.push({
          name: 'OctAddPlanRange',
          params: {
            mode: 'change',
            plan: item
          }
        }))
      }
    },
    async handleDeletePlan (item) {
      const data = {
        planId: item.planId
      }
      await deletePlan(data)
        .then(() => {
          this.$notify.success('删除成功')
          this.$emit('reload')
        })
    }
  }
}
</script>

<style scoped lang="scss">
  .plan-single-issue {
    display: flex;
    flex-direction: row;
    position: relative;
    margin: 10px 0;
    padding: 15px 20px;
    border-radius: 10px;
    background-color: rgba(41, 153, 8, 0.1);
  }
  .plan-single-issue-solve {
    display: flex;
    flex-direction: row;

    position: relative;
    margin: 10px 0;
    padding: 15px 20px;
    border-radius: 10px;
    background-color: rgba(144, 147, 153, 0.2);
  }
  .plan-issue-date {
    font-size: $great-font;
    font-weight: bold;
    color: $max-color;
  }
  .plan-issue-date-medium {
    font-size: $large-font;
    font-weight: bold;
    color: $max-color;
  }
  .plan-issue-date-small {
    font-size: $normal-font;
    color: #5c5c5c;
  }
  .plan-issue-date-content {
    font-size: $normal-font;
    color: #343434;

  }
  .plan-issue-icon {
    font-size: 25px;
    color: white;
    background-color: chocolate;
    border-radius: 50%;
    width: 35px;
    height: 35px;
    margin-right: 10px;

    .plan-issue-btn {
      margin: 0 10px;
    }
  }
  .plan-solve {
    position: absolute;
    right: 0;
    bottom: 0;
    border: 35px solid rgba(41, 153, 8, 0.6);
    border-left: 55px solid transparent;
    border-top: 25px solid transparent;
    border-bottom-right-radius: 10px;
    width: 0;
  }
  .plan-solved {
    position: absolute;
    right: 0;
    bottom: 0;
    border: 35px solid rgba(144, 147, 153, 0.8);
    border-left: 55px solid transparent;
    border-top: 25px solid transparent;
    border-bottom-right-radius: 10px;
    width: 0;
  }
  .plan-solve-content {
    position: absolute;
    right: 8px;
    bottom: 5px;
    color: whitesmoke;
  }
  .close-icon {
    font-size: 20px;
    margin-top: 10px;
  }
  .close-icon-two {
    font-size: 20px;
  }
</style>
