<template>
  <div class="plan-main-range">
    <div class="float-button oct-flex-col-all-center">
      <el-button circle type="text" class="round-btn" @click="addPlan(0)"><el-icon :class="showConfig ? 'el-icon-arrow-down' : 'el-icon-plus'"></el-icon></el-button>
    </div>
    <transition name="el-zoom-in-bottom">
      <div v-show="showConfig" class="float-button-one">
        <div class="float-button-two oct-flex-col-all-center">
          <el-button circle type="text" class="round-btn" @click="addPlan(1)">阶段</el-button>
        </div>
        <div class="float-button-two oct-flex-col-all-center">
          <el-button circle type="text" class="round-btn" @click="addPlan(2)">当日</el-button>
        </div>
      </div>
    </transition>
    <div class="float-button-today oct-flex-col-all-center">
      <el-button circle type="text" class="round-btn-today" @click="moveToday">今</el-button>
    </div>
    <van-calendar
      id="popCanlender"
      color="rgb(165, 145, 108)"
      @select="selectDate"
      :poppable="false"
      :max-date="maxDate"
      :min-date="minDate"
      :default-date="defaultDate"
      :show-confirm="false"
      :formatter="formatter"
      :style="{ height: '390px' }">
    </van-calendar>
    <van-tabs v-model="active"
              style="margin: 0 -10px 110px -10px;"
              lazy-render
              sticky
              type="card"
              background="rgb(235, 232, 221)"
              color="rgba(165, 145, 108, 1)"
              :ellipsis="false">
      <van-tab title="当日计划"><oct-show-plan-list ref="today" :type="1" :status="1" :plans="plans" @reload="reload"></oct-show-plan-list></van-tab>
      <van-tab title="阶段计划"><oct-show-plan-list ref="range" :type="2" :status="1" :plans="plans" @reload="reload"></oct-show-plan-list></van-tab>
      <van-tab title="当日已完成"><oct-show-plan-list ref="todayDone" :type="1" :status="2" :plans="plans" @reload="reload"></oct-show-plan-list></van-tab>
      <van-tab title="阶段已完成"><oct-show-plan-list ref="rangeDone" :type="2" :status="2" :plans="plans" @reload="reload"></oct-show-plan-list></van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { getPlanByPage } from '@/api/study'
import OctShowPlanList from '@/components/plan/OctShowPlanList'

export default {
  name: 'OctPlanCalender',
  components: { OctShowPlanList },
  data () {
    return {
      active: '',
      showConfig: false,
      showAddPlan: true,
      selectTime: new Date(),
      maxDate: new Date(new Date().getFullYear() + 1, 12, 31),
      minDate: new Date(new Date().getFullYear() - 1, 1, 1),
      defaultDate: new Date(),
      plans: []
    }
  },
  mounted () {
    this.initData()
    this.moveToday()
  },
  activated () {
    this.moveToday()
  },
  watch: {
    selectTime () {
      this.changeSelectStyle()
    }
  },
  methods: {
    changeSelectStyle () {
      const dom = document.getElementsByClassName('van-calendar__selected-day')[0]
      dom.innerHTML = dom.innerHTML.substring(0, dom.innerHTML.indexOf('</div>') + 6)
      const year = this.selectTime.getFullYear()
      const month = this.selectTime.getMonth() + 1
      const date = this.selectTime.getDate()
      let allSub = ''
      let allRange = ''
      this.plans.forEach((item) => {
        let planYear = new Date(item.startTime).getFullYear()
        let planMonth = new Date(item.startTime).getMonth() + 1
        let planDay = new Date(item.startTime).getDate()
        if (item.type === 1) {
          if (year === planYear && month === planMonth && date === planDay) {
            allSub += item.subject
            allSub += ' '
          }
        } else {
          if (year === planYear && month === planMonth && date === planDay) {
            allRange += item.subject
            allRange += ' '
          }
          planYear = new Date(item.endTime).getFullYear()
          planMonth = new Date(item.endTime).getMonth() + 1
          planDay = new Date(item.endTime).getDate()
          if (year === planYear && month === planMonth && date === planDay) {
            allRange += item.subject
            allRange += ' '
          }
        }
      })
      setTimeout(function () {
        const dom = document.getElementsByClassName('van-calendar__selected-day')[0]
        let content = '<div class="van-calendar__top-info">' + allSub + '</div>'
        let contentBottom = '<div class="van-calendar__bottom-info">' + allRange + '</div>' + date
        let res = content + contentBottom
        if (dom.innerHTML.indexOf(res) === -1) {
          dom.innerHTML = res
        }
      }, 1)
    },
    reload () {
      this.initData()
      // if (type === 1 && status === 1) {
      //   this.$refs.todayDone.reloadData()
      // }
      // if (type === 1 && status === 2) {
      //   this.$refs.today.reloadData()
      // }
      // if (type === 2 && status === 1) {
      //   this.$refs.rangeDone.reloadData()
      // }
      // if (type === 2 && status === 2) {
      //   this.$refs.range.reloadData()
      // }
    },
    async initData () {
      const data = {
        startPage: -1
      }
      await getPlanByPage(data)
        .then((res) => {
          this.plans = []
          if (res.data.data.length !== 0) {
            this.plans = res.data.data
            // 修改日历选中的样式
            this.changeSelectStyle()
          }
        })
    },
    moveToday () {
      document.getElementsByClassName('van-calendar__body')[0].scrollTop = (document.getElementsByClassName('van-calendar__month')[11].offsetTop - 80)
    },
    selectDate (date) {
      this.showAddPlan = true
      this.selectTime = date
      // 修改vant样式,修改选中当天看不到当天计划的缺陷
    },
    formatter (day) {
      const year = day.date.getFullYear()
      const month = day.date.getMonth() + 1
      const date = day.date.getDate()
      this.plans.forEach((item) => {
        if (item.type === 1) {
          const planYear = new Date(item.startTime).getFullYear()
          const planMonth = new Date(item.startTime).getMonth() + 1
          const planDay = new Date(item.startTime).getDate()
          if (year === planYear && month === planMonth && date === planDay) {
            if (day.topInfo !== undefined) {
              day.topInfo += item.subject
            } else {
              day.topInfo = item.subject
            }
            day.topInfo += ' '
          }
        } else if (item.type === 2) {
          let planYear = new Date(item.startTime).getFullYear()
          let planMonth = new Date(item.startTime).getMonth() + 1
          let planDay = new Date(item.startTime).getDate()
          if (year === planYear && month === planMonth && date === planDay) {
            if (day.bottomInfo !== undefined) {
              day.bottomInfo += item.subject
            } else {
              day.bottomInfo = item.subject
            }
            day.bottomInfo += ' '
          }
          planYear = new Date(item.endTime).getFullYear()
          planMonth = new Date(item.endTime).getMonth() + 1
          planDay = new Date(item.endTime).getDate()
          if (year === planYear && month === planMonth && date === planDay) {
            if (day.bottomInfo !== undefined) {
              day.bottomInfo += item.subject
            } else {
              day.bottomInfo = item.subject
            }
            day.bottomInfo += ' '
          }
        }
      })
      // if (month === 5) {
      //   if (date === 1) {
      //     day.topInfo = '劳动节'
      //   } else if (date === 4) {
      //     day.topInfo = '五四青年节'
      //   } else if (date === 11) {
      //     day.text = '今天'
      //   }
      // }
      //
      // if (day.type === 'start') {
      //   day.bottomInfo = '入住'
      // } else if (day.type === 'end') {
      //   day.bottomInfo = '离店'
      // }
      return day
    },
    addPlan (num) {
      this.showConfig = !this.showConfig
      if (num === 2) {
        this.$router.push(this.$router.push({
          name: 'OctAddPlan',
          params: {
            mode: 'add',
            date: this.selectTime
          }
        }))
      }
      if (num === 1) {
        this.$router.push(this.$router.push({
          name: 'OctAddPlanRange',
          params: {
            mode: 'add',
            date: this.selectTime
          }
        }))
      }
    }
  }
}
</script>

<style lang="scss">
  .van-calendar__month-mark {
    color: rgba(165,145,108,0.2) !important;
  }
  .plan-main-range {
    padding: 10px;
    .van-calendar {
      background-color: $bg-color;
      border-radius: 10px;
      margin: 5px 10px 10px 10px;
    }
    .van-calendar__header-title {
      display: none !important;
    }
    .van-calendar__month-title {
      margin-left: 20px;
      text-align: left !important;
      font-size: $large-font;
      font-weight: bold;
    }
  }
</style>
<style lang="scss" scoped>
  .float-button {
    z-index: 9999;
    width: 15vw;
    height: 15vw;
    position: fixed;
    bottom: 60px;
    right: 7vw;
    border-radius: 50%;
    background-color: whitesmoke;
    border: 0.5px solid rgba(165, 145, 108);
  }
  .float-button-one {
    z-index: 9999;
    width: 15vw;
    height: 15vw;
    position: fixed;
    bottom: calc(140px + 15vw + 10px);
    right: 7vw;
  }
  .float-button-two {
    /*z-index: 9999;*/
    width: 15vw;
    height: 15vw;
    /*position: fixed;*/
    /*bottom: calc(120px + 15vw + 20px);*/
    right: 7vw;
    border-radius: 50%;
    background-color: whitesmoke;
    border: 0.5px solid rgba(165, 145, 108);
    margin-top: 10px;
  }
  .float-button-today {
    z-index: 9999;
    width: 15vw;
    height: 15vw;
    position: fixed;
    bottom: 60px;
    right: 25vw;
    border-radius: 50%;
    background-color: rgba(165, 145, 108);
  }
  .round-btn {
    font-size: 5vw;
    padding: 0 !important;
    border: none;
    color: rgba(165, 145, 108, 0.9);
  }
  .round-btn-today {
    font-size: 7vw;
    padding: 0 !important;
    border: none;
    color: whitesmoke;
  }

</style>
